/*=======================NAVIGATION STYLES======================*/

#header {
display: flex;
flex-direction: row;
justify-content: space-between;
align-items: center;
background-color: black;
padding: 15px 15px 15px 30px;
box-shadow: 0 1px 6px 0 rgba(32,33,36,0.4);
}
.headroom-wrapper{
height: 100px;
}
.subHeader {
display: flex;
flex-direction: row;
align-items: center;
}
.App-logo {
height: 7vmin;
pointer-events: none;
}
.App-logo-span {
color: #FFF;
font: 35px Raleway;
margin: 0px 10px;
padding: 10px 10px; 
position: relative;
z-index: 0;
pointer-events: none;
}
.navLinks {
font-family: Raleway;
margin: 0 auto;
text-align: center;
}
.navLinks a {
color: #FFF;
text-decoration: none;
font: 20px Raleway;
margin: 0px 10px;
padding: 20px 20px;
position: relative;
z-index: 0;
cursor: pointer;
}

/* ==============NAVIGATION ANIMATION==================== */

.circleBehind a:before, .circleBehind a:after {
position: absolute;
top: 22px;
left: 50%;
width: 50px;
height: 50px;
border: 4px solid #1d3f72;
-webkit-transform: translateX(-50%) translateY(-50%) scale(0.8);
        transform: translateX(-50%) translateY(-50%) scale(0.8);
border-radius: 50%;
background: transparent;
content: "";
opacity: 0;
-webkit-transition: all 0.3s;
transition: all 0.3s;
z-index: -1;
}
.circleBehind a:after {
border-width: 2px;
-webkit-transition: all 0.4s;
transition: all 0.4s;
}
.circleBehind a:hover:before {
opacity: 1;
-webkit-transform: translateX(-50%) translateY(-50%) scale(1);
        transform: translateX(-50%) translateY(-50%) scale(1);
}
.circleBehind a:hover:after {
opacity: 1;
-webkit-transform: translateX(-50%) translateY(-50%) scale(1.3);
        transform: translateX(-50%) translateY(-50%) scale(1.3);
}
