@import 'https://fonts.googleapis.com/css?family=Raleway';
@import url('//maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

html {
background: black;
}

body{
font-family: Raleway;
height: 5000px;
}

/*=======================NAVIGATION STYLES======================*/

#header {
display: flex;
flex-direction: row;
justify-content: space-between;
align-items: center;
background-color: black;
padding: 15px 15px 15px 30px;
-webkit-box-shadow: 0 1px 6px 0 rgba(32,33,36,0.28);
box-shadow: 0 1px 6px 0 rgba(32,33,36,0.4);
}
.headroom-wrapper{
height: 100px;
}
.subHeader {
display: flex;
flex-direction: row;
align-items: center;
}
.App-logo {
height: 7vmin;
pointer-events: none;
}
.App-logo-span {
color: #FFF;
font: 35px Raleway;
margin: 0px 10px;
padding: 10px 10px; 
position: relative;
z-index: 0;
pointer-events: none;
}
.navLinks {
font-family: Raleway;
margin: 0 auto;
text-align: center;
}
.navLinks a {
color: #FFF;
text-decoration: none;
font: 20px Raleway;
margin: 0px 10px;
padding: 20px 20px;
position: relative;
z-index: 0;
cursor: pointer;
}

/* ==============NAVIGATION ANIMATION==================== */

.circleBehind a:before, .circleBehind a:after {
position: absolute;
top: 22px;
left: 50%;
width: 50px;
height: 50px;
border: 4px solid #1d3f72;
transform: translateX(-50%) translateY(-50%) scale(0.8);
border-radius: 50%;
background: transparent;
content: "";
opacity: 0;
transition: all 0.3s;
z-index: -1;
}
.circleBehind a:after {
border-width: 2px;
transition: all 0.4s;
}
.circleBehind a:hover:before {
opacity: 1;
transform: translateX(-50%) translateY(-50%) scale(1);
}
.circleBehind a:hover:after {
opacity: 1;
transform: translateX(-50%) translateY(-50%) scale(1.3);
}
/*========================ABOUT STYLES========================*/
#about{
display: flex;
flex-direction: column;
align-items: center;
position: relative;
top: 2.5rem;
}
.mypic {
border-radius: 100rem;
height: 45vmin;
width: 45vmin;
box-shadow: 0 1px 6px 0 rgba(32,33,36,0.7);
}
/***========================UNDER PIC HEADING AND SUB-HEADING STYLES========================***/
h1 {
font-weight: 700;
font-size: 35px;
margin-top: 1rem;
margin-bottom: 1rem;
}
h2 {
font-weight: 500;
font-size: 20px;
}
/***========================SOCIAL MEDIA LINK STYLES/ANIMATIONS========================***/

.socialbtns, .socialbtns ul, .socialbtns li {
margin: 5px;
margin-bottom: 4rem;
padding: 5px;
text-align: center;
}
.socialbtns li {
list-style: none outside none;
display: inline-block;
}
.socialbtns .fa {
color: #FFF;
background-color: #000;
padding: 10px;
border-radius: 200px;
-moz-border-radius: 200px;
-webkit-border-radius: 200px;
-o-border-radius: 200px;
transition: all ease 2s;
-moz-transition: all ease 2s;
-webkit-transition: all ease 2s;
-o-transition: all ease 2s;
transform: rotate(-360deg);
-moz-transform: rotate(-360deg);
-webkit-transform: rotate(-360deg);
-o-transform: rotate(-360deg);
box-shadow: 0 1px 6px 0 rgba(32,33,36,0.28);
}
.socialbtns .fa:hover {
transition: all ease 1s;
-moz-transition: all ease 1s;
-webkit-transition: all ease 1s;
-o-transition: all ease 1s;
transform: rotate(360deg);
-moz-transform: rotate(360deg);
-webkit-transform: rotate(360deg);
-o-transform: rotate(360deg);
}


/***========================SKILLS SECTION STYLES========================***/

.skills {
background-image: url('Components/assets/web.jpg');
background-attachment: fixed;
background-position: center;
background-repeat: no-repeat;
background-size: cover;
color: black;
height: 40rem;
width: 100%;
}
.skillsIcons {
display: flex;
flex-direction: row;
color: white;
text-align: center;
flex-wrap: wrap;
padding-top: 3rem;
padding-left: 2rem;
padding-right: 2rem;
width: 100%;
}
.subSkills{
display: flex;
flex-direction: column;
align-items: center;
width: 25%;
padding-right: 20px;
padding-left: 20px;
}
.fa-laptop-code, .fa-address-card, .fa-handshake, .fa-running {
font-size: 5rem;
padding-bottom: 5px;
}
.fa-laptop-code:hover, .fa-address-card:hover, .fa-handshake:hover, .fa-running:hover {
display: inline-block;
-moz-animation: pulse 2s infinite linear;
-o-animation: pulse 2s infinite linear;
-webkit-animation: pulse 2s infinite linear;
animation: pulse 2s infinite linear;
}
.skillsSpan {
font-size: 28px;
padding-bottom: 10px;
font-weight: 600;
}
.skillsP {
margin: 0px; 
}

