flex-direction: row;
align-items: center;
margin-left: 5rem;
margin-right: 5rem;
margin-bottom: 5rem;
font-size: 30px;
}
.cardRight{
display: flex;
flex-direction: row-reverse;
align-items: center;
margin-left: 5rem;
margin-right: 5rem;
margin-bottom: 5rem;
font-size: 30px;
}
/***========================FOOTER STYLES========================***/
.footer {
